<template>
  <div
    class="wrapper pa-4"
  >
    <h1>Drawing lines</h1>
    <canvas
      id="c"
      class="full-bleed"
    />

    <highlightjs
      class="mt-4"
      :code="`//..
//  
const material1 = new THREE.LineBasicMaterial( { color: 0x0000ff } );
const points = [];
points.push( new THREE.Vector3( - 10, 0, 0 ) );
points.push( new THREE.Vector3( 0, 10, 0 ) );
points.push( new THREE.Vector3( 10, 0, 0 ) );
//  
const geometry1 = new THREE.BufferGeometry().setFromPoints( points );
const line = new THREE.Line( geometry1, material1 );
scene.add(line);
//..`"
      :language="'js'"
    />
  </div>
</template>

<script type="module">
import * as THREE from 'three';

export default {
  data() {
    return {
      fov: 75, // 
      aspect: 2, //   canvas300x150300/1502
      near: 0.1, // 
      far: 5, // 

      boxWidth: 1,
      boxHeight: 1,
      boxDepth: 1,
    };
  },
  mounted() {
    this.main();
  },
  methods: {
    main() {
      // canvas
      const canvas = document.querySelector('#c');
      //  antialias
      const renderer = new THREE.WebGLRenderer({antialias: true, canvas});
      renderer.setSize( window.innerWidth, window.innerHeight - 300, false );
      const camera = new THREE.PerspectiveCamera(90,  window.innerWidth / window.innerHeight, 1, 500 );

      //  xyz
      camera.position.set( 0, 0, 15 );
      camera.lookAt( 0, 0, -0.2 );

      // 
      const scene = new THREE.Scene();

      //    
      const geometry = new THREE.BoxGeometry(1, 1, 1);

      // 
      const material = new THREE.MeshPhongMaterial({
        color: '#FFDD11',
      });

      // (Mesh) geometry( ) material( ..)
      const cube = new THREE.Mesh(geometry, material);
      
      // 
      scene.add(cube);

      //  
      const material1 = new THREE.LineBasicMaterial( { color: 0x0000ff } );
      const points = [];
      points.push( new THREE.Vector3( - 10, 0, 0 ) );
      points.push( new THREE.Vector3( 0, 10, 0 ) );
      points.push( new THREE.Vector3( 10, 0, 0 ) );
      //  
      const geometry1 = new THREE.BufferGeometry().setFromPoints( points );
      const line = new THREE.Line( geometry1, material1 );
      scene.add(line);

      // 
      renderer.render(scene, camera);

      // 
      const color = 0xFFFFFF;
      const intensity = 3;
      const light = new THREE.DirectionalLight(color, intensity);
      light.position.set(-1, 2, 4); // 
      scene.add(light);

      // 
      function resizeRendererToDisplaySize(renderer) {
        const canvas = renderer.domElement;
        const width = canvas.clientWidth;
        const height = canvas.clientHeight;
        const needResize = canvas.width !== width || canvas.height !== height;
        if (needResize) {
          renderer.setSize(width, height, false);
        }
        return needResize;
      }

      // 
      function render(time) {
        time *= 0.001;  // 

        // canvastrue  
        if (resizeRendererToDisplaySize(renderer)) {
          const canvas = renderer.domElement;
          camera.aspect = canvas.clientWidth / canvas.clientHeight;
          camera.updateProjectionMatrix();
        }
      
        cube.rotation.x = time;
        cube.rotation.y = time;
      
        renderer.render(scene, camera);
      
        requestAnimationFrame(render);
      }
      requestAnimationFrame(render);
    },
  }
}
</script>

<style scoped>
.wrapper {
  display: grid;
  grid-template-columns:
    1fr
    min(65ch, 100%)
    1fr;
}

.wrapper > * {
  grid-column: 2;
}

.full-bleed {
  width: 100%;
  grid-column: 1 / -1;
}

</style>